import React from 'react';
import styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const LargeMenu = styled.div`
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
    height: 100%;
    @media screen and (max-width: ${props => props.size}px) {
        display: none;
    }
    width: 100vw;
    background-color: #242526;
    opacity: 0.8;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
`;

const SmallMenu = styled(LargeMenu)`
    display: none;
    justify-content: space-around;
    @media screen and (max-width: ${props => props.size}px) {
        display: flex;
    }
`;

const Container = styled.div`
    position: fixed;
    top: 0px;
    height: 55px;
`;

const NavItem = styled.div`
    color: #f2f5f9;
    font-family: 'Raleway', sans-serif;
    text-transform: uppercase;
    letter-spacing: 0.4em;
`;

class Header extends React.Component {
    state = {
        smallMenuOpen: false,
    };
    render() {
        return (
            <Container>
                <LargeMenu size={1000}>
                    <NavItem>Hello</NavItem>
                    <NavItem>Main</NavItem>
                    <NavItem>You</NavItem>
                </LargeMenu>
                <SmallMenu size={1000}>
                    <NavItem>Main</NavItem>
                    <button
                        className={
                            'hamburger hamburger--spin' +
                            (this.state.smallMenuOpen ? ' is-active' : null)
                        }
                        type="button"
                        onClick={() =>
                            this.setState({
                                smallMenuOpen: !this.state.smallMenuOpen,
                            })
                        }
                    >
                        <span className="hamburger-box">
                            <span className="hamburger-inner" />
                        </span>
                    </button>
                </SmallMenu>
            </Container>
        );
    }
}

export default Header;
